.register_container {
    min-height: 100vh;
    width: 100%;
    background-image: url("../../public/assests/images/sellerlogin.png");
    background-repeat: no-repeat;
    background-size: 70%;
    background-position: 42vw 3vh;
    position: relative;
  }
  
  .register_box_main {
    /* min-height: 80vh; */
    height: fit-content;
    width: 32vw;
    background-color: rgba(255, 255, 255, 0.8);
    /* border: 1px solid red; */
    position: absolute;
    border-radius: 25px;
    top: 50%;
    left: 25%;
    transform: translateY(-50%);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-block: 8vh;
    /* filter: blur(5px); */
  }
  
  .register_box_main h1 {
    /* text-align: center; */
    font-size: 2rem;
    color: #333333;
    font-family: Gorditas ;
    margin-block: 0vh 3vh;
  }
  
  .input_main {
    /* border: 1px solid red; */
    width: 70%;
  }
  .input_main h3 {
    font-family: var(--primary-font);
    font-weight: 500;
    font-size: 1.05rem;
  }
  
  .switch_btn {
    /* border: 1px solid red; */
    display: flex;
    /* color: #FFFFFF; */
    gap: 10px;
    justify-content: center;
    padding-bottom: 20px;
  }
  .switch_btn button {
    /* background-color: transparent; */
    border: none;
    font-weight: 600;
    cursor: pointer;
  }
  .greenBg {
    background-color: #58B310; /* or any other green color you prefer */
  }
  .active {
    /* background-color: var(--primary) !important; */
    background-color: #58B310;
    border-radius: 8px;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25);
    color: #FFFFFF;
    transition: all 0.18s ease-in;
    font-size: 1.08rem;
    height: 35px;
    width: 140px;
  }
  .inactive {
    transition: all 0.18s ease-in;
    font-size: 1rem;
    height: 35px;
    width: 125px;
  }
  
  .inputs {
    display: flex;
    flex-direction: column;
    gap: 3.2vh;
    margin-block: 2vh;
    /* border: 1px solid red; */
    position: relative;
  }
  .inputs p {
    color: var(--secondary);
    font-family: var(--primary-font);
    font-weight: 500;
    margin-bottom: 5px;
  }
  .inputs h6 {
    position: absolute;
    bottom: -25px;
    right: 0px;
    font-size: 0.85rem;
    font-weight: 400;
    font-family: var(--primary-font);
  }
  
  .input_main input {
    height: 40px;
    width: 100%;
    border: none;
    box-shadow: 1px 1.2px 2px 2px rgb(209, 199, 199);
    box-shadow: 0px 4px 4px rgb(209, 199, 199);
    padding-inline: 10px;
    font-size: 1.02rem;
    background-color: transparent;
  }
  .inputs input:focus {
    outline: none;
  }
  
  .action_button {
    margin-block: 3vh 0vh;
    display: flex;
    flex-direction: column;
    gap: 2vh;
    align-items: center;
  }
  
  .submit button {
    padding: 8px 15px;
    font-size: 1.02rem;
    background-color: var(--primary);
    color: var(--body);
    border: none;
    border-radius: 7px;
    box-shadow: 0.5px 1px 5px black;
    font-weight: 600;
    cursor: pointer;
  }
  .submit button:hover {
    background-color: var(--neutral);
    color: var(--black);
  }
  
  .login_options {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    font-family: var(--primary-font);
    font-weight: 500;
    font-size: 0.9rem;
  }
  .login_options > span {
    display: flex;
    gap: 5px;
  }
  .btn_group {
    display: flex;
    gap: 20px;
  }
  